.dashboard {
    background: var(--primary-color);
    display: flex;
}

.dashboard-contents {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    gap: 1rem;
}

.charts {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    height: 100%;
    justify-content: space-between;
}